.main-nav__toggle {
  position: absolute;
  top: 23px;
  right: 20px;
  z-index: 2;
  border: none;
  cursor: pointer;
  outline: none;
}

.main-nav__list {
  margin: 0 -20px;
  padding: 0;
  list-style: none;
  display: none;
  text-align: center;
  background-color: $smoky-grey;
}

.main-nav__item {
  padding: 4px 26px 0 0;
  border-top: 1px solid rgba(0, 0, 0, 0.05);

  &:first-child {
    border: none;
  }
}

.main-nav__link {
  display: block;
  padding: 13px 0;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
  font-family: "Circe", "Arial", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: $grey-shadow;
}

.main-nav__link[href] {
  &:hover {
    color: black;
  }
}

@media (max-width: $desktop-width - 1) {
  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--closed .main-nav__toggle,
  .main-nav--opened .main-nav__toggle {
    width: 20px;
    height: 2px;
    border-radius: 1px;
    background-color: $grey-cloud;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 7px;
      right: 0;
      display: block;
      width: 100%;
      height: 2px;
      border-radius: 1px;
      background-color: $grey-cloud;
    }

    &::after {
      top: 14px;
    }
  }

  .main-nav--opened .main-nav__list {
    display: block;
  }

  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
}

@media (min-width: $tablet-width) {
  .main-nav__toggle {
    left: 167px;
  }

  .main-nav__list {
    position: absolute;
    top: 56px;
    left: 0;
    width: 226px;
    overflow: hidden;
  }
}

@media (min-width: $desktop-width) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav {
    max-width: 370px;
  }

  .main-nav__list {
    position: static;
    display: flex;
    justify-content: space-between;
    width: auto;
    background-color: white;
  }

  .main-nav__item {
    margin-left: 18px;
    padding-right: 0;
    border-top: none;
  }

  .main-nav__link {
    font-size: 10px;
  }
}
